--><html><head></head><body><h1>CesiumTerrainProvider</h1>
<p><code v-pre="">vc-provider-terrain-cesium</code> 组件用于加载 Cesium 格式的地形，如果<code v-pre="">url</code>为空，默认通过 <code v-pre="">Cesium.createWorldTerrain</code> 加载 CesiumIon 在线全球地形。</p>
<h2>示例</h2>
<h3>加载 Cesium 格式地形</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-layer-imagery>
          <vc-provider-imagery-bingmaps url="https://dev.virtualearth.net" bm-key="AmGu3cvB_g1HbkQErEyvmLc9j0YIGWS7IdOqR7-hQbO8J92Fzrzkhy_bYKSsyoEx" map-style="Aerial"></vc-provider-imagery-bingmaps>
        </vc-layer-imagery>
        <vc-provider-terrain-cesium ref="terrain" :request-water-mask="requestWaterMask"></vc-provider-terrain-cesium>
        <vc-navigation></vc-navigation>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
