      <span class="hljs-tag">&lt;<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-provider-imagery-bingmaps</span>
          <span class="hljs-attr">url</span>=<span class="hljs-string">"https://dev.virtualearth.net"</span>
          <span class="hljs-attr">bm-key</span>=<span class="hljs-string">"AmGu3cvB_g1HbkQErEyvmLc9j0YIGWS7IdOqR7-hQbO8J92Fzrzkhy_bYKSsyoEx"</span>
          <span class="hljs-attr">map-style</span>=<span class="hljs-string">"Aerial"</span>
        &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-imagery-bingmaps</span>&gt;</span>
