    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-title function_">data</span>(<span class="hljs-params"></span>) {
      <span class="hljs-keyword">return</span> {}
    },
    <span class="hljs-attr">methods</span>: {
      <span class="hljs-title function_">ready</span>(<span class="hljs-params">cesiumInstance</span>) {
        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer } = cesiumInstance
        <span class="hljs-keyword">var</span> target = <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">Cartesian3</span>(<span class="hljs-number">300770.50872389384</span>, <span class="hljs-number">5634912.131394585</span>, <span class="hljs-number">2978152.2865545116</span>)
        <span class="hljs-keyword">var</span> offset = <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">Cartesian3</span>(<span class="hljs-number">6344.974098678562</span>, -<span class="hljs-number">793.3419798081741</span>, <span class="hljs-number">2499.9508860763162</span>)
        viewer.<span class="hljs-property">camera</span>.<span class="hljs-title function_">lookAt</span>(target, offset)
        viewer.<span class="hljs-property">camera</span>.<span class="hljs-title function_">lookAtTransform</span>(<span class="hljs-title class_">Cesium</span>.<span class="hljs-property">Matrix4</span>.<span class="hljs-property">IDENTITY</span>)
      }
